@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
.navbar{display:-webkit-flex;display:flex;width:100vw;max-width:100%;background-color:black;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.navbar__search{width:clamp(200px, 35vw, 400px);height:clamp(35px, 6vw, 45px);font-size:14px;font-weight:400;outline-color:#efeff1;background-color:rgba(255,255,255,0.2);border:2px solid rgba(255,255,255,0);border-top-left-radius:5px;border-bottom-left-radius:5px;color:white;padding-left:10px;margin-top:7px;margin-bottom:7px;transition:border 0.3s}.navbar__search:focus{outline:none;border:2px solid #a16feb}.navbar__icon{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;border:0px solid rgba(255,255,255,0);background-color:rgba(255,255,255,0.15);border-top-right-radius:5px;border-bottom-right-radius:5px;width:clamp(40px, 7vw, 53px);height:clamp(35px, 6vw, 45px);cursor:pointer;margin-left:1px}.navbar__icon__svg{color:white;font-size:clamp(20px, 3.5vw, 27px)}.button{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;color:#e7dada;-webkit-justify-content:flex-start;justify-content:flex-start;margin:40px 0px 5px 50px}.button__option{cursor:pointer;font-size:20px;font-weight:500;margin:0px 15px 20px 15px;font-family:"Poppins",sans-serif;transition:color 0.2s ease}.button__option:hover{color:#a16feb}.topGames{display:grid;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));grid-auto-rows:auto;grid-gap:32px;padding:0px 50px 0px 50px}.topGames__game{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;margin:10px;-webkit-justify-content:center;justify-content:center}.topGames__game__img{width:100%;max-width:290px;height:auto}.topGames__game__name{text-align:left;width:100%}.topGames__game__name__text{color:white;font-weight:400;font-size:20px;font-family:"Roboto", sans-serif;transition:color 0.2s ease;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.topGames__game__name__text:hover{color:#a16feb}@media (max-width: 900px){.button{margin-left:30px}.topGames{padding:0px 30px 0px 30px;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr))}}@media (max-width: 768px){.button{margin-left:12px}.topGames{padding:0px 12px 0px 12px;grid-template-columns:repeat(auto-fit, minmax(180px, 1fr))}}@media (max-width: 630px){.button{margin-left:10px}.topGames{padding:0px 10px 0px 10px;grid-template-columns:repeat(auto-fit, minmax(150px, 1fr));grid-gap:5px}}@media (max-width: 480px){.button{-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}}@media (max-width: 350px){.topGames{padding:0px 2px 0px 2px;grid-template-columns:repeat(auto-fit, minmax(130px, 1fr));grid-gap:3px}.topGames__game__img{max-width:130px}}.topStreamers{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.topStreamers__stream{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:10px;-webkit-justify-content:center;justify-content:center}.topStreamers__stream__thumbnail{display:-webkit-flex;display:flex;position:relative}.topStreamers__stream__thumbnail__count{position:absolute;bottom:10px;left:10px;color:white;padding:5px 10px;border-radius:3px;background-color:rgba(255,255,255,0.5)}.topStreamers__stream__info{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;min-width:100px;max-width:250px;margin:0 auto;text-align:flex-start;-webkit-flex-direction:column;flex-direction:column}.topStreamers__stream__info__title{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-family:"Poppins",sans-serif;font-weight:500;color:#f0dede}.topStreamers__stream__info__username{font-family:"Poppins",sans-serif;font-weight:400;color:#dfc6c6;font-size:14px}.topStreamers__stream__info__game{font-family:"Poppins",sans-serif;font-weight:400;color:#dfc6c6;font-size:14px}.streamWrapper{display:-webkit-flex;display:flex;width:100vw;height:calc(100vh - clamp(50px, 6vw, 60px));-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;max-width:100%;max-height:100%}.twitchEmbed{display:-webkit-flex;display:flex;width:100%;max-width:100%;height:100%;max-height:100%}p{margin:0px;padding:0px}a{text-decoration:none;color:white}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;background-color:#17171a;color:#8892b0;font-family:"Kanit", sans-serif;-webkit-font-smoothing:antialiased}

